// $u.mixin.js

import { mapState } from 'vuex'
import store from "@/store"

// store/index.jsvuexstate
let $uStoreKey = [];
try{
	$uStoreKey = store.state ? Object.keys(store.state) : [];
}catch(e){
	
}

module.exports = {
	mounted() {
		this.$u.vuex = (name, value) => {
			this.$store.commit('$uStore', {
				name,value
			})
		}
	},
	computed: {
		// vuexstatemixin
		...mapState($uStoreKey)
	}
}
